.main {
  flex: 1 1 auto;
  text-align: center;
  padding-top: 50px;

  @media (min-width: 768px) {
        padding-top: 80px;
  }
}

.main__photo {
    height: 250px;

    border-radius: 5px;
    object-fit: cover;
    object-position: right;

    @media (max-width: 767px) {
        width: 100%;
    }

    @media (min-width: 768px) {
        height: 100%;
    }
}
