.logo {
    flex-shrink: 0;
    width: 40px;
    height: 40px;
    border-radius: 50%;

    overflow: hidden;
    position: relative;
    z-index: 3;

    @media (min-width: 768px) {
        width: 60px;
        height: 60px;
}

}

.logo__img {
    width: 100%;
    height: 100%;
}

.logo img {
    max-width: 100%;
    display: block;
}
